.movieinfo_arrange{
    display: grid;
    grid-gap: 10px;
    grid-template-columns:  repeat(auto-fill,minmax(500px,1fr));
}
.trailer{
    width:620px;
    height:400px;
    margin:20px;
    margin-top: 50px;
    display: flex;
    justify-content: center;
}
@media screen and (max-width: 500px){
    .trailer{
        margin:0 auto;
        margin-top: 10px;
    }
}
.trailersmall{
    width:400px;
    height:270px;
    margin:0 auto;
    margin-top:30px;
    display: flex;
    justify-content: center;
}
.movieinfo_cast{
    display: grid;
    grid-gap: 10px;
    
    grid-template-columns:  repeat(auto-fill,minmax(180px,1fr));
}
.wallpaper{
    padding:20px;
    margin:20px;
    background-color: rgb(0, 0, 0,0.75);
    height:500px;
    width:60%;
    height:auto;
    /* max-width: 400px; */
}
.infocard{
    width:78%;
    /*max-width: 600px;*/
}
.actorbackg{
    background-color: rgb(0, 0, 0,0.55);
    /*min-height: 300px;*/
}
.similar-movies{
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fill,minmax(250px,1fr)); ;
}
.similar-movie-box{
    background-color: rgb(0, 0, 0,0.55);
    padding:10px;
}