.grid_class{
    display: grid;
    grid-gap: 5px;
    grid-template-columns:  repeat(auto-fill,minmax(250px,1fr));
}
.card{
    margin:10px;
    padding:10px;
    background-color: rgb(0, 0, 0,0.55);
}
.empty{
    height:501px;
}
.movie_name{
    min-height: 65px;
    margin:10px;
}
.movie_pic{
    min-height: 400px;
}