.navunli{
    list-style-type: none;
    display: flex;
    flex-flow: row-reverse;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: larger;
}
.navlist{
    margin:1px 10px 1px 10px;
    float: right;
}
.position_set{
    
    display: grid;
    grid-gap:1px;
    grid-template-columns: 1fr 3fr;
    background-color: transparent;
    
}

