.history{
    display: flex;
    flex-flow: row;
}
@media screen and (max-width: 700px){
    .history{
        display: flex;
        flex-flow: column;
    }
}
.historywhole{
    border:1px solid black;
    margin: 0 auto;
    background-color: rgb(0,0,0,0.50);
    border-radius: 5px;
    width: 90%;
}

.historysingle{
    
    border-radius: 5px;
    margin: 10px;
    font-size: 20px;
    padding: 2px;
    
}
.historysingle:hover{
    background-color: rgba(0,0,0,0.55);
    
}
.history_heading{
    font-size: 35px;
}
@media screen and (max-width: 700px){
    .history_heading{
        font-size: 25px;
    }
}
.sidebutton-whole{
    background-color: rgba(0,0,0,0.55);
    width: min-content;
    margin:0 auto;
    padding:30px;
    height:min-content;
    margin-top: 2 0px;
    display: flex;
    flex-flow: column;
}
@media screen and (max-width: 700px){
    .sidebutton-whole{
        display: flex;
        flex-flow: row;
        padding: 10px;
    }
}
.sidebutton{
    margin: 15px;
    font-size: 20px;
    width:max-content;
    text-align: center;
}
@media screen and (max-width: 700px){
    .sidebutton{
        margin:5px;
            font-size: 12px;
    }
}
.control_panel
{
    margin:0 auto;
    margin-top:60px;
}
@media screen and (max-width: 700px){
    .control_panel{
        margin-top:30px;
    }
}