.welcome_heading{
    margin-top: 150px;
    font-size: 60px;
}
@media screen and (max-width: 1000px){
    .welcome_heading{
        margin-top: 120px;
        font-size: 45px;
    }
}
@media screen and (max-width: 500px){
    .welcome_heading{
        margin-top: 45px;
        font-size: 45px;
    }
}
.welcome_message{
    font-size: 30px;
}
@media screen and (max-width: 1000px){
    .welcome_message{
        font-size: 25px;
        font-weight: bold;
    }
}
.email_box{
    width:40%;
    min-width: 350px;   
    height:57px;
}
@media screen and (max-width: 1000px){
    .email_box{  
        height:47px;
    }
}
.space{
    border:  solid rgb(56, 56, 56);
    border-width: 0px 0px 8px 0px;
}
.welcome_info{
    background-color: rgba(0,0,0,0.9);
    border:  solid rgb(56, 56, 56);
    border-width: 0px 0px 8px 0px;
    /*display: grid;
    grid-gap: 40px;
    grid-template-columns:  repeat(auto-fill,minmax(480px,1fr));*/
    /*grid-template-columns: 1fr 1fr;*/
    display: flex;
    flex-flow: row;
    padding: 50px;
}
@media screen and (max-width: 1000px){
    .welcome_info{
        flex-flow: column;
        
    }
}
.welcome_pics{
    width:55%;
    height:auto;
    margin:50px;
}
@media screen and (max-width: 1000px){
    .welcome_pics{
        width:80%;
        height:auto;
        margin:10%;
    }
}
.welcome_pic_info_heading{
    /*margin-top: 100px;*/
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 40px;
}
@media screen and (max-width: 1000px) {
	.welcome_pic_info_heading{
    
        /*margin-left:30px;*/
        font-size: 30px;
    }
}
.welcome_pic_info{
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 25px;
}
@media screen and (max-width: 1000px) {
    .welcome_pic_info{
        
        width:100%;
        font-size: 20px;
        
    }
}
.info{
    margin:2%;
    width:auto;
}
@media screen and (max-width: 500px){
    .info{
        /*width:80%;*/
        width:auto;
    }
}

